<template>
  <router-link
    class="button"
    :class="{ fullwidth: fullwidth }"
    :to="link"
    :style="{ background: background, color: color }"
    >{{ title }}</router-link
  >
</template>
<script>
export default {
  name: "BlogButton",
  props: {
    title: {
      type: String,
      default() {
        return "click here";
      },
    },
    background: {
      type: String,
      default() {
        return "#4095cf";
      },
    },
    color: {
      type: String,
      default() {
        return "#fff";
      },
    },
    link: {
      type: String,
      default() {
        return "/";
      },
    },
    fullwidth: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
};
</script>
<style scoped>
.button {
  display: inline-block;
  border: none;
  outline: none;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 10px;
  padding: 0.5rem;
  margin: 0.5rem;
  cursor: pointer;
  text-decoration: none;
  transition: 0.4s;
}
.button:hover {
  filter: brightness(110%);
  box-shadow: 0 0 13px 2px lightblue;
}
.fullwidth {
  display: block;
  width: 100%;
  text-align: center;
  margin: auto;
}
</style>
