<template>
  <div class="card" :style="{ background: background }">
    <div v-show="showMedia" class="card-media">
      <slot name="media"></slot>
    </div>
    <div class="card-ditails">
      <div v-show="showHeader" class="card-header">
        <slot name="header"></slot>
      </div>
      <div v-show="showContent" class="card-content">
        <slot name="content"></slot>
      </div>
      <div v-show="showAction" class="card-actions">
        <slot name="action"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    background: {
      type: String,
      default() {
        return "#bdbdbd";
      },
    },
    showMedia: {
      type: Boolean,
    },
    showHeader: {
      type: Boolean,
    },
    showAction: {
      type: Boolean,
    },
    showContent: {
      type: Boolean,
      default() {
        return true;
      },
    },
  },
};
</script>
<style scoped>
.card {
  /* background: #87dfcff0; */
  border: 2px solid #eee;
  padding: 1rem;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  margin: 1rem auto;
  box-shadow: 1px 1px 10px 3px #eee;
  transition: 0.4s;
}
/* .card-media {
  width: 45%;
} */
/* .card-ditails {
  width: 45%;
} */
.card-media img {
  width: 100%;
}
.card:hover {
  /* cursor: crosshair; */
  box-shadow: none;
}
.card-header {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.card-content {
  font-size: 1rem auto;
}
</style>
